* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', "Arial", sans-serif;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.container {
  max-width: 1100px;
  margin: 0 auto;
}
.body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', "Arial", sans-serif;
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.body--scrolloff {
  overflow: hidden;
}
.header {
  position: fixed;
  z-index: 1;
  width: 100%;
  max-height: 100%;
  padding: 0 20px;
  box-shadow: 0px 6px 10px #cccccc;
  background-color: #ffffff;
}
@media (max-width: 900px) {
  .header {
    grid-template-columns: 1fr;
    overflow-x: auto;
  }
}
.header__wrap {
  width: 100%;
  display: grid;
  grid-template-columns: min-content 1fr;
  grid-template-rows: 1fr;
  background-color: #ffffff;
}
@media (max-width: 900px) {
  .header__wrap {
    grid-template-columns: 1fr;
  }
}
.page-title {
  margin: 15px 0;
  font-size: 28px;
  font-family: 'Akshar', sans-serif;
  font-weight: bold;
  color: #000000;
  white-space: nowrap;
}
@media (max-width: 900px) {
  .page-title {
    position: absolute;
    top: 20px;
    left: 20px;
    z-index: 1;
    margin: 0;
    font-size: 32px;
  }
}
.nav {
  display: flex;
  align-items: center;
  justify-content: end;
}
@media (max-width: 900px) {
  .nav {
    flex-direction: column;
    align-items: end;
    justify-content: start;
    gap: 20px;
  }
}
.nav__button {
  display: none;
  border: none;
  background-color: transparent;
}
@media (max-width: 900px) {
  .nav__button {
    display: block;
    width: 80px;
    height: 80px;
  }
}
.nav__list {
  height: 100%;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: end;
  gap: 20px;
}
@media (max-width: 900px) {
  .nav__list {
    display: none;
  }
}
.nav__list--open {
  display: flex;
  flex-direction: column;
  align-items: end;
  height: min-content;
  padding-bottom: 20px;
}
.nav__item {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav__link {
  height: 100%;
  font-size: 19px;
  white-space: nowrap;
  position: relative;
  padding: 15px 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Roboto', "Arial", sans-serif;
  color: #000000;
  text-decoration: none;
  opacity: 0.8;
}
@media (max-width: 900px) {
  .nav__link {
    font-size: 22px;
  }
}
.nav__link--active {
  opacity: 1;
  pointer-events: none;
}
.nav__link--active::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #cccccc;
}
.nav__link:hover,
.nav__link:focus {
  opacity: 1;
}
.nav__link:hover::before,
.nav__link:focus::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #cccccc;
  animation-duration: 1s;
  animation-name: linkanimate;
}
@keyframes linkanimate {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}
.nav__shopping-cart {
  width: 35px;
  height: 35px;
}
.btn-icon {
  height: 75px;
  width: 75px;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.btn-icon--active {
  transform: rotate(45deg);
}
.btn-icon__line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: #000000;
  stroke-width: 5.5;
  stroke-linecap: round;
}
.btn-icon__line--top {
  stroke-dasharray: 40 139;
}
.btn-icon__line--bottom {
  stroke-dasharray: 40 180;
}
.btn-icon--active .btn-icon__line--top {
  stroke-dashoffset: -98px;
}
.btn-icon--active .btn-icon__line--bottom {
  stroke-dashoffset: -138px;
}
.main {
  padding: 100px 20px;
}
.main--index {
  flex-grow: 1;
}
.main--catalog {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr;
  grid-row-gap: 30px;
}
.main__wrap-cart {
  position: relative;
  display: grid;
  grid-template-columns: 1fr min-content;
  grid-template-rows: min-content;
  grid-column-gap: 40px;
}
@media (max-width: 900px) {
  .main__wrap-cart {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, min-content);
    grid-row-gap: 80px;
  }
}
.footer {
  padding: 20px;
  box-shadow: 0px -6px 10px #cccccc;
}
.footer__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 600px) {
  .footer__wrap {
    flex-direction: column;
    gap: 30px;
  }
}
.social-list {
  display: flex;
  align-items: center;
  justify-content: start;
  gap: 20px;
  list-style: none;
}
@media (max-width: 600px) {
  .social-list {
    flex-direction: column;
  }
}
.social-list__link {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 0;
  text-decoration: none;
  color: #000000;
  font-family: 'Roboto', "Arial", sans-serif;
  opacity: 0.8;
  font-size: 17px;
}
.social-list__link:hover,
.social-list__link:hover .social-list__icon,
.social-list__link:focus,
.social-list__link:focus .social-list__icon {
  opacity: 1;
}
.social-list__icon {
  opacity: 0.5;
}
.rights {
  font-family: 'Roboto', "Arial", sans-serif;
  font-size: 15px;
  opacity: 0.7;
}
.banner {
  width: 100%;
  height: 300px;
  position: relative;
}
.banner__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.banner__info {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 10px 20px;
  background-color: rgba(255, 255, 255, 0.6);
}
.banner__title {
  font-size: 22px;
  font-family: 'Roboto', "Arial", sans-serif;
  font-weight: 500;
}
.banner__text {
  font-size: 18px;
  font-family: 'Roboto', "Arial", sans-serif;
  font-weight: 300;
}
.offer {
  width: 100%;
  height: 300px;
}
.offer__title {
  font-size: 28px;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  text-transform: capitalize;
}
.catalog {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr;
  grid-row-gap: 20px;
}
.catalog__title {
  font-size: 28px;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  text-transform: capitalize;
}
.catalog__list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: center;
}
.catalog__list--cart {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content;
  grid-gap: 10px;
}
.catalog__item {
  width: 280px;
  background: #fff;
  box-shadow: 20px 20px 40px #cecece, -20px -20px 40px #f2f2f2;
}
.catalog__item--cart {
  width: 100%;
  height: min-content;
}
.product-card {
  width: 100%;
  height: 100%;
  padding-bottom: 20px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, min-content) 1fr;
}
.product-card--cart {
  grid-template-columns: 150px 1fr;
  grid-template-rows: min-content min-content;
  padding-bottom: 0;
}
.product-card__title {
  order: 2;
  font-size: 20px;
  font-family: 'Roboto', "Arial", sans-serif;
  font-weight: 500;
  padding: 10px;
}
.product-card__description {
  order: 3;
  font-size: 17px;
  font-family: 'Roboto', "Arial", sans-serif;
  font-weight: 300;
  color: #000000;
  padding: 10px;
}
.product-card__img-link {
  width: 280px;
  height: 280px;
  overflow: hidden;
}
.product-card__img-link--cart {
  grid-row-start: 1;
  grid-row-end: -1;
  height: 100%;
  width: 100%;
  display: flex;
}
.product-card__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: flex;
  transition: 0.3s ease-in-out;
}
.product-card__img:hover,
.product-card__img:focus {
  transform: scale(1.3);
  transition: 0.3s ease-in-out;
}
.product-cart__buy-block {
  order: 4;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: start;
  gap: 2px;
}
.product-cart__buy-info {
  font-size: 17px;
  font-family: 'Roboto', "Arial", sans-serif;
  font-weight: 300;
}
.product-card__show-info {
  width: min-content;
  padding: 10px;
  display: flex;
  align-items: center;
  order: 4;
  font-size: 17px;
  font-family: 'Roboto', "Arial", sans-serif;
  font-weight: 400;
  text-decoration: underline;
  color: #bebebe;
  transition: all ease-in 0.2s;
  cursor: pointer;
}
.product-card__show-info:hover,
.product-card__show-info:focus {
  color: #000000;
}
.cart {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content min-content 1fr;
  grid-row-gap: 20px;
}
.cart__title {
  font-weight: 500;
  font-size: 24px;
  font-family: 'Roboto', "Arial", sans-serif;
}
.cart__count {
  padding: 10px 0;
  font-weight: 400;
  font-size: 18px;
  font-family: 'Roboto', "Arial", sans-serif;
}
.cart-info {
  position: sticky;
  top: 200px;
  height: min-content;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, min-content);
  grid-row-gap: 20px;
}
@media (max-width: 900px) {
  .cart-info {
    grid-row-start: 1;
    grid-row-end: 2;
    position: static;
  }
}
.cart-info__title {
  font-family: 'Roboto', "Arial", sans-serif;
  font-size: 28px;
  font-weight: 500;
}
.cart-info__list {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
  padding-left: 20px;
  gap: 10px;
  position: relative;
}
.cart-info__list::before {
  position: absolute;
  bottom: -10px;
  left: 0;
  content: '';
  width: 100%;
  height: 1px;
  background-color: #bebebe;
}
.cart-info__item {
  font-family: 'Roboto', "Arial", sans-serif;
  padding: 3px 0;
  font-size: 17px;
  font-weight: 300;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: flex-start;
}
.cart-info__item::after {
  position: absolute;
  content: '';
  left: -20px;
  top: 50%;
  transform: translateY(-50%);
  width: 10px;
  height: 10px;
  background-color: #bebebe;
  border-radius: 50%;
}
.cart-info__item-info {
  font-family: 'Roboto', "Arial", sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 25px;
}
.cart-info__sum {
  font-family: 'Roboto', "Arial", sans-serif;
  font-size: 18px;
  font-weight: 400;
}
.cart-info__buy-button {
  cursor: pointer;
  font-family: 'Roboto', "Arial", sans-serif;
  font-size: 18px;
  font-weight: 400;
  color: #ffffff;
  width: 300px;
  padding: 20px;
  border: none;
  background-color: #000000;
}
.product {
  display: grid;
  grid-template-columns: min-content 1fr;
  grid-template-rows: repeat(3, min-content);
  grid-template-areas: 'title title' 'img info' 'controls info';
  grid-row-gap: 30px;
  grid-column-gap: 40px;
}
@media (max-width: 900px) {
  .product {
    grid-column-gap: 15px;
    grid-row-gap: 20px;
  }
}
@media (max-width: 700px) {
  .product {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, min-content);
    grid-template-areas: 'title ' 'img' 'info' 'controls';
  }
}
.product__title {
  grid-area: title;
  font-family: 'Open Sans', "Arial", sans-serif;
  font-size: 30px;
  font-weight: 500;
}
.product__image {
  grid-area: img;
  object-fit: cover;
}
@media (max-width: 900px) {
  .product__image {
    width: 300px;
    height: 400px;
  }
}
@media (max-width: 700px) {
  .product__image {
    object-fit: contain;
    width: 100%;
    height: 400px;
  }
}
.product__info {
  grid-area: info;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: start;
}
.product__info-title {
  font-family: 'Open Sans', "Arial", sans-serif;
  font-size: 28px;
  font-weight: 500;
}
.product__sub-title {
  font-family: 'Open Sans', "Arial", sans-serif;
  margin-bottom: 30px;
  font-size: 16px;
  font-weight: 300;
}
.product__text {
  font-family: 'Open Sans', "Arial", sans-serif;
  margin-bottom: 15px;
  font-size: 17px;
  font-weight: 300;
}
.product__bold-text {
  font-weight: 500;
}
.effects__list {
  padding-left: 20px;
}
.effects__item {
  padding: 5px 0;
}
.effects__text {
  font-family: 'Open Sans', "Arial", sans-serif;
}
.price-table {
  table-layout: fixed;
  border-collapse: collapse;
  margin: 20px 0;
}
@media (max-width: 700px) {
  .price-table {
    width: 100%;
  }
}
.price-table__title {
  font-family: 'Open Sans', "Arial", sans-serif;
  font-size: 18px;
  font-weight: normal;
  padding: 5px;
  text-align: left;
}
@media (max-width: 700px) {
  .price-table__title {
    text-align: center;
  }
}
.price-table__costs {
  font-family: 'Open Sans', "Arial", sans-serif;
  font-size: 18px;
  font-weight: 500;
  padding: 5px;
  text-align: center;
}
.product__controls {
  grid-area: controls;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: start;
  gap: 40px;
}
@media (max-width: 700px) {
  .product__controls {
    align-items: center;
    gap: 20px;
  }
}
.product__socials {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: start;
  gap: 15px;
}
@media (max-width: 700px) {
  .product__socials {
    align-items: center;
    text-align: center;
  }
}
.product__socials-title {
  font-family: 'Open Sans', "Arial", sans-serif;
  font-size: 20px;
  font-weight: 400;
}
.product__button {
  position: relative;
  font-family: 'Open Sans', "Arial", sans-serif;
  cursor: pointer;
  min-width: 180px;
  min-height: 45px;
  padding: 20px 60px 20px 20px;
  border: none;
  border-radius: 8px;
  background-color: #000000;
  color: #ffffff;
  font-size: 17px;
  font-weight: 300;
  opacity: 1;
}
.product__button::before {
  content: '';
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  width: 45px;
  height: 45px;
  background-repeat: no-repeat;
